@use 'Base/variables' as v;
@use 'Base/mixins' as m;

.HeroInvitado {
    margin: 20px;
    padding: 20px;
    height: auto;
    overflow-y: auto;
    background-color: v.$blanco;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    display: block;
    @include m.tablet {
        display: block;
        max-width: calc(100% - 40px);
        margin-top: 20px;
        margin-bottom: 20px;
    }
    // @include m.desktop{
    //     display: flex;
    // }
    .consultaForm__titulo {
        color: v.$negro;
        font: v.$titulos;
        margin: 0;
        font-size: 2.5rem;
        text-align: center;
        margin-bottom: 1rem;

        @include m.tablet {
            font-size: 4rem;
        }
    }

    .HeroInvitadoImg {
        // border-radius: 50%;
        overflow: hidden;
        object-fit: cover;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 200px;
        height: 200px;
        margin-top: 2rem;
        margin-bottom: 2rem;
        margin: auto;

        @include m.tablet {
            width: 100%;
            height: 100%;
        }

        @include m.desktop{
            display: flex;
        }

        .contentImg {
            @include m.tablet {
                width: 150px;
                height: 150px;
            }

        }
    }

    .HeroInvitadolabel {
        font-size: 1.6rem;
        margin-bottom: 5px;

        @include m.tablet {
            font-size: 2rem;
        }
    }

    .HeroInvitadoinput {
        @include m.input(v.$blanco, #ecedec, v.$primario, v.$gri);
    }

}
    .consultaInfo{
        display: block;
         @include m.desktop{
           display: flexbox;
        }
    }

.BotonesEyS {
    display: flex;
    justify-content: space-between;
    margin: auto;

    @include m.tablet {
        display: flex;
        gap: 2rem;
        max-width: 50rem;
    }

    &__Entrada {
        @include m.button(v.$secundario, v.$secundario-hover, v.$negro, v.$blanco);
        width: auto;
        text-align: center;

        @include m.tablet {
            width: auto;
        }
    }

    &__Salida {
        @include m.button(v.$primario, v.$primario-hover, v.$blanco, v.$blanco);
        width: auto;
        text-align: center;

        @include m.tablet {
            width: auto;
        }
    }

    &__Guardar {
        @include m.button(v.$secundario, v.$secundario-hover, v.$negro, v.$blanco);
        margin: auto;
        width: auto;
        text-align: center;

        @include m.tablet {
            width: auto;
        }
    }

}

.HeroTablaConsultaInvitado{
    padding-top: 3rem;
    width: 90%;
    display: block;
    margin: auto;
    @include m.tablet{
        width: 80%;
    }
    thead{
        background-color: v.$secundario;
    }
    th {
        font-size: 1.5rem;
            @include m.tablet{
                font-size: 2rem;
            }
    }

    h2 {
        text-align: center;
        font-size: 2.3rem;
    }
    
    .icon-trash {
        width: 40px;
        height: 30px;
        background: none;
        border: none;
    }

    .icon-trash img {
        width: 100%;
        height: 100%;
    }
}

