@use 'Base/variables' as v;
@use 'Base/mixins' as m;

#mainContainer{
    margin: 0;
}
.errorcuatro{
    width: 90%;
    height: 85vh;
    display: flex;
    justify-content: center;
    margin: 0 auto;
    // display: block;
    @include m.tablet{
        width:100%;
        display: block;
        margin-left: 0%;
    }
        &__text{
            //background-color: v.$blanco;
            display: flex;
            flex-direction: column;
            width: 80%;
            text-align: center;
            margin: auto;
            padding-top: 10%;
            align-items: center;
            height: auto;
                h1{
                    font-size: 1rem;
                }
                @include m.tablet{
                    position: absolute;
                    top: 30%;
                    left: 10%;
                }
                h1{
                    font-weight: bold;
                    color: v.$primario;
                    font-size: 6rem;
                }
                h2{
                    color: v.$secundario-hover;
                    font-size: 4rem;
                }
        }

        &__background{
            margin-left: 0;
            
            &__nubes{
                display: flex;
                position: absolute;
                top: 2%;
                left: 40%;
                .nube1{
                    width: 100%;
                }
                .nube2{
                    display: none;
                }
                @include m.tablet{
                    top:2%;
                    left: 5%;
                    display: flex;
                    justify-content: space-between;
                    width: 90%;
                    margin-top: 6%;
                    margin-left: 0;      
                    .nube2 {
                        display: flex;
                        width: 20%;
                         }
                     .nube1{
                     display: flex ;
                     width: 20%;
                    }
                }
       
                 }
            &__bee{
                display: flex;
                position: absolute;
                top:80%;
                left: 35%;
                width: 25%;
                    @include m.tablet{
                        top:60%;
                        left:15%;
                        width: 20%;
                    }
                    @include m.desktop{
                        top:50%;
                        left: 20%;
                        width:30%;
                        img{
                            transform: rotate(-40deg);
                            width: 50%;
                        }
                    }
            }
            &__edificios{
                display: flex;
                position: absolute;
                bottom: 0;
                margin: auto;
                    .edificio2,
                    .edificio3{
                        display: none;
                    }
                @include m.tablet{
                    justify-content: space-between;
                    position: absolute;
                    width: 100%;
                    display: flex;
                    bottom: 0%;
                    align-items:center;
                    &__img{
                        width: 33%;
                        height: 100%;
                        .edificio1{
                            width: 100%;
                            height: 100%;
                            max-height: 100%;
                            object-fit: cover;
                        }
                        .edificio2{
                            height: 100%;
                            display: flex;
                            width: 100%;
                            object-fit: cover;
                        }
                        .edificio3{
                            display: flex;
                            width: 100%;
                            object-fit: cover;
                        }
                    }
                    &__img:last-child{
                        width: 22%;
                        .edificio3{
                            display: flex;
                            width: 100%;
                            object-fit: cover;
                        }
                    }
                }
            }
        }
}

.slide-tl {
    -webkit-animation: slide-tl 3s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
    animation: slide-tl 3s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
}
@-webkit-keyframes slide-tl {
    0% {
      -webkit-transform: translateY(0) translateX(0) rotate(0);
              transform: translateY(0) translateX(0) rotate(0);
    }
    100% {
      -webkit-transform: translateY(-100px) translateX(-100px) rotate(-45deg);
              transform: translateY(-100px) translateX(-100px) rotate(-45deg);
    }
  }
  @keyframes slide-tl {
    0% {
      -webkit-transform: translateY(0) translateX(0) rotate(0);
              transform: translateY(0) translateX(0) rotate(0);
    }
    100% {
      -webkit-transform: translateY(-100px) translateX(-100px) rotate(-45deg);
              transform: translateY(-100px) translateX(-100px) rotate(-45deg);
    }
  }