@use 'Base/variables' as v;
@use 'Base/mixins' as m;

/* Personaliza los eventos para que sean ms compactos */
.fc-event {
    height: auto;
    font-size: 12px;
    overflow: hidden;
}

/* Estilo para la expansin de eventos */

/* Fondo oscuro para el modal */
.modal-background {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    z-index: 99;
}

.calendarMargin #seccionCalendario{
    display: flex;
    flex-direction: column;
    align-items: start;
    justify-content: center;
    gap: 4rem;
    @include m.tablet{
        align-items: center;
    }
}
.event-details {
    background: white;
    border: 1px solid #ccc;
    padding: 10px;
}

.fc-daygrid-day-events{
    cursor: pointer;
}

.calendarMargin{
    margin: 0 5% !important;
    @include m.tablet{
        margin-left: 12% !important;
        width: 86% !important;
    }
}

#calendar{
    width: 100%;
    @include m.tablet{
        width: auto;
    }
}

#seccionCalendario{
    max-width: 95%;
    @include m.tablet{
        max-width: 100%;
    }
}

@media (min-width: 768px){
    .calendarMargin #seccionCalendario{
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        grid-template-rows: 1fr;
    }
    #calendar{
        grid-column: 1 / 4;
        margin: 0 !important;
    }
    #eventModal{
        grid-column: 4 / 5;
    }
    .event-details{
        margin-left: 2rem;
    }
}

.event-details{
    background-color: #305272;
    color: v.$blanco;
    display: flex;
    flex-direction: column;
    align-items: center;
    border-radius: 2rem;
    width: 90%;
    margin: 0 auto;
    @include m.tablet{
        min-width: 0;
    }

    h3{
        text-align: center;
        margin-bottom: 2rem;
    }
    button{
        margin: 1rem 0;
        @include m.button(v.$blanco, v.$secundario, v.$primario, v.$blanco);
        width: 100% !important;
        font-size: 1.4rem !important;
        height: 4rem !important;
    }
    }
#modalEvents{
    font-size: 2rem;
    width: 90%;
}

.tituloReunionEvent{
    font-size: 1.4rem;
    }
.detallesReunionEvent{
    margin-bottom: -1rem;
    color: v.$blanco;
    font-size: 1.2rem;
}
.tantasReunionesEvent{
    font-size: 1.4rem;
    color: v.$blanco;
    text-align: center;
    margin-bottom: -1rem;
}

#eventModal{
    width: 100%;
    @include m.tablet{
        width: auto;
    }
}

.fc-col-header-cell-cushion{
    font-size: 1.4rem;
    @include m.tablet{
        font-size: 100%;
    }
}

.CrearReunionButton{
    @include m.button(v.$primario, v.$secundario, v.$blanco, v.$blanco);
    max-width: 80%;
    @include m.tablet{
        max-width: 40%;
    }
}

.cReunionesTitulo{
    text-align: center;
    font-size: 4.2rem;
    margin: 4rem 0;
}