@use 'Base/variables' as v;
@use 'Base/mixins' as m;
.containerScan{
    background-color: v.$blanco;
    justify-content: center;
    width: 90%;
    margin: auto;
    padding: 10px;
    border-radius: 5px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
        @include m.tablet{
            width: 80%;  
        }
        @include m.desktop{
            width: 50%;
            padding: 5%;
        }
}

#btn-scan-qr{
    img{
        margin:auto !important;
    }
   
}
.botonesScan{
    display: block;
    align-items: center;
    justify-content: space-between;

}
.ContenedorBotonScan{
    text-align: center
}
.botonScanOn{
    @include m.button(v.$primario, v.$primario-hover, v.$blanco, v.$blanco);
    width: 80%;  
    height: 50%;
    text-align: center;
}
.botonScanOff{
    @include m.button(v.$secundario, v.$secundario-hover, v.$negro, v.$negro);
    width: 80%;  
    height: 50%;
    text-align: center;
}
