@use 'Base/variables' as v;
@use 'Base/mixins' as m;

.crearReunion {
  background-color: v.$blanco;
  padding: 4rem 1.5rem;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  border-radius: 8px;

  .div__reuniones {
    border-top: 2px solid v.$gris;
    border-bottom: 2px solid v.$gris;
    min-height: 0;
    margin-top: 2rem;
    margin-bottom: 3rem;
    padding: 1.5rem 0;
    gap: 2rem;

  }

  #AgendarReunion {
    margin-bottom: 2rem;

  }

  .Formulario {
    .contenedor_flex {
      display: flex;
      flex-direction: center;
      gap: 2rem;

      padding: 4rem 1.5rem;

      @include m.tablet {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
      }

      div {
        max-width: 100%;
      }
    }

    background-color: v.$blanco;
    padding: rem;
    box-shadow: 10px 10px 8px -3px rgba(0, 0, 0, 0.33);
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 2rem;
    border: none;
    padding: 1.5rem;


    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    &__inputGroup {
      &__label {
        font-size: 1.6rem;

        @include m.tablet {
          font-size: 2rem;
          max-width: calc(100% - 40px);
        }
      }

      &__input {
        @include m.input(v.$blanco, #ecedec, v.$primario, v.$gri);
      }

      &__opCheck {
        display: flex;
        align-items: center;
      }

      select {
        border: 1.5px solid #ecedec;
        border-radius: 10px;
        height: 50px;
        display: flex;
        align-items: center;
        transition: 0.2s ease-in-out;
        margin-bottom: 1rem;
        font-size: 1.4rem;
        width: 100%;
      }

      option {
        padding-left: 1rem;
      }

      select:focus {
        outline: none;
        border-color: v.$secundario;
      }

      select option:hover {
        background-color: rgb(240, 240, 197);
        color: #000;
      }

      select option:focus {
        background-color: rgb(250, 255, 189);
        color: #000;
      }
    }

    &__tableContainer {
      width: 80%;
      display: flex;
      align-items: center;
      flex-direction: column;
      text-align: center;

      &__tabla {
        tr {
          td:first-child {
            width: 50%;

            input {
              position: relative;
              color: black;
              @include m.input(v.$blanco, #ecedec, v.$primario, v.$gri);
            }
          }

          td:last-child {
            button {
              @include m.button(v.$primario, v.$primario-hover, v.$blanco, v.$blanco);
              margin-top: 0;
            }
          }
        }

      }

      .addFecha__boton {
        margin-top: 2rem;
        @include m.button(v.$secundario, v.$secundario-hover, v.$negro, v.$blanco)
      }
    }

  }

  .actualizarBoton {
    @include m.button(v.$primario, v.$primario-hover, v.$blanco, v.$blanco);
  }

  .addFecha__boton {
    margin-bottom: 2rem;
    background-color: v.$secundario;
    color: v.$negro;
    transition: v.$tran-btn;
    padding: 0.5rem 0.5rem;
    font-weight: 500;
    text-decoration: none;
    //text-transform: uppercase;
    font-size: 13px;
    letter-spacing: 0.1rem;
    vertical-align: middle;
    border: none;
    border-radius: 1rem;
    width: 35%;
    height: 35px;
    padding: 1rem 2rem;

    @include m.tablet {
      width: 25%;
    }

    &:hover {
      background-color: v.$secundario-hover;
      color: v.$blanco;
      transition: v.$tran-btn;
    }
  }

  .button__submit {
    @include m.button(v.$primario, v.$primario-hover, v.$blanco, v.$blanco);
    margin: 2rem auto;
  }

  .button__container {
    display: flex;
    justify-content: center;
    margin: 2rem 0;
  }
}

.form__inv {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  form {
    width: 80%;

    @include m.tablet {
      width: 50%;
      margin: 0 auto;
    }
  }
}

.boton__inv {
  @include m.button(v.$primario, v.$primario-hover, v.$blanco, v.$blanco);
  margin: 2rem 0;
  width: auto !important;
}