@use "Base/mixins" as m;
@use "Base/variables" as v;

#DetallesReunion{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 3rem 0;
    padding: 2rem 0;
    border-bottom: solid 1px #111111;
    border-top: solid 1px #111111;
    li{
        font-size: 1.6rem;
    }
    p{
        span{
            font-weight: bold;
        }
    }
}
.labelControl{
    font-size: 1.5rem;
}
.buttonAcepatarReunion{
    @include m.button(v.$primario, v.$blanco, v.$blanco, v.$negro);
}

.cRegistro{
    .title-example{
        margin: 2rem 0;
    }
    form{
        .buttonContainer{
            width: 100%;
            display: flex;
            justify-content: center;
        }
        .acompanantes, .dispositivos, .automoviles{
            margin: 2rem 0;
            padding: 2rem 0;
            border-bottom: 1px solid #c5c5c5;
            @include m.tablet{
                border-bottom: none;
            }
        }
        .acompanantes{
            display: flex;
            flex-direction: column-reverse;
            justify-content: center;
            align-items: center;
            @include m.tablet{
                display: grid;
                grid-template-columns: repeat(3, 1fr);
                grid-template-rows: 1 / 1fr;
                gap: 2rem;
                // justify-content: space-evenly;
                div{
                    grid-column: 1/3;
                    grid-row: 1/2;
                }
            }

            .instruccion{
                color: v.$primario;
                font-size: 1.8rem;
                text-align: center;
                margin-bottom: 2rem;
                font-weight: bold;
            }
            #BotonAgregarAcompanante{
                display: flex;
                justify-content: center;
                margin-top: 2rem;
            }

        }
        .dispositivos{
            display: flex;
            flex-direction: column-reverse;
            justify-content: center;
            align-items: center;
            text-align: center;
            @include m.tablet{
                display: grid;
                grid-template-columns: repeat(3, 1fr);
                grid-template-rows: 1 / 1fr;
                gap: 2rem;
                // justify-content: space-evenly;
                div{
                    grid-column: 1/3;
                    grid-row: 1/2;
                }
            }
            .instruccion{
                color: v.$primario;
                font-size: 1.8rem;
                text-align: center;
                margin-bottom: 2rem;
                font-weight: bold;
            }
            #BotonAgregarDispositivo{
                display: flex;
                justify-content: center;
                margin-top: 2rem;
            }
            .form-group{
                margin-bottom: 2rem;
            }
        }
        .automoviles{
            display: flex;
            flex-direction: column-reverse;
            justify-content: center;
            align-items: center;
            text-align: center;
            @include m.tablet{
                display: grid;
                grid-template-columns: repeat(3, 1fr);
                grid-template-rows: 1 / 1fr;
                gap: 2rem;
                // justify-content: space-evenly;
                div{
                    grid-column: 1/3;
                    grid-row: 1/2;
                }
            }
            .instruccion{
                color: v.$primario;
                font-size: 1.8rem;
                text-align: center;
                margin-bottom: 2rem;
                font-weight: bold;
            }
            #BotonAgregarAutomovil{
                display: flex;
                justify-content: center;
                margin-top: 2rem;
            }
            .form-group{
                margin-bottom: 2rem;
            }
        }
        .buttonAcepatarReunion{
            border: 1px solid #c0c0c0;
        }

        #acompanantesAgregados, #dispositivosAgregados, #automovilesAgregados{
            @include m.tablet{
                padding-left: 2rem;
                border-left: 1px solid #c0c0c0;
                min-height: 20rem;
                grid-column: 3 / 4;
                grid-row: 1/2;
            }
            span{
                font-size: 1.4rem;
                text-transform: uppercase;
                font-weight: bold;
                @include m.tablet{
                    font-size: 1.6rem;
                }
            }
            text-align: center;
            .comoTal{
                margin-bottom: 0.25rem;
                margin-top: 0.5rem;
                span{
                    font-size: 1.2rem;
                }
                font-size: 1.2rem;
            }
        }
    }
}

.mb-25rem{
    margin-bottom: 2.5rem;
}