@use 'Base/variables' as v;
@use 'Base/mixins' as m;

.consultarDatosReunion {
    .consultar_titulo {
        margin-bottom: 1.3rem;

        @media (max-width: 832px) {
            margin-bottom: 0.8rem;
            font-size: 25px;
        }

        @media (max-width: 582px) {
            margin-bottom: 0.6rem;
        }
    }

    .botones {
        display: flex;
        justify-content: center;
        align-items: center;
        align-content: center;
        flex-wrap: nowrap;

        button {
            transition: v.$tran-btn;
            font-weight: 500;
            text-decoration: none;
            font-size: 20px;
            letter-spacing: 0.1rem;
            vertical-align: middle;
            border: none;
            border-radius: 1rem;
        }

        .reagendar_btn,
        .reagendar30m_btn,
        .reagendar1h_btn,
        .regresar_btn {
            margin: 2.5rem 1.5rem 0rem 1.5rem;
            width: 28%;
            height: 4rem;
            background-color: v.$secundario;
            color: v.$negro;

            @media (max-width: v.$phone) {
                height: 4rem;
                font-size: 18px;
                padding: 0.5rem 1rem;
            }

            &:hover {
                background-color: v.$secundario-hover;
                color: v.$blanco;
                transition: v.$tran-btn;
            }
        }

        .cancelar_btn {
            margin: 2.5rem 1.5rem 0rem 1.5rem;
            width: 28%;
            height: 4rem;
            background-color: v.$primario;
            color: v.$blanco;


            @media (max-width: v.$phone) {
                height: 4rem;
                font-size: 18px;
                padding: 0.5rem 1rem;
            }

            &:hover {
                background-color: v.$primario-hover;
                color: v.$blanco;
                transition: v.$tran-btn;
            }
        }

        @media (max-width: 832px) {
            flex-direction: column;

            .reagendar_btn,
            .reagendar30m_btn,
            .reagendar1h_btn,
            .cancelar_btn {
                width: 100%;
                margin: 1rem auto;
            }
        }
    }

    .Detalle {
        //Div
        background-color: v.$blanco;
        padding: 4rem;
        box-shadow: 10px 10px 8px -3px rgba(0, 0, 0, 0.33);

        .DetalleReunion {
            //Form
            display: flex;
            flex-wrap: wrap;
            margin-bottom: 2rem;
            border: none;
            padding: 1.5rem;

            .Ninput {
                @include m.input(v.$blanco, #ecedec, v.$primario, v.$gri);
            }

            .Nlabel {
                font-size: 1.6rem;

                @include m.tablet {
                    font-size: 2rem;
                    max-width: calc(100% - 40px);

                }
            }
        }

        //Configuracin de la tabla de invitados
        .__Tabla {
            @media (max-width: v.$tablet) {
                font-size: 1.4rem;
            }

            @media (max-width: v.$phone) {
                font-size: 1.1rem;
            }

            .tabla {
                .tabla__cabecera {
                    tr {
                        .cabecera {
                            background-color: v.$tabla-cabecera;
                            color: v.$tabla-cabecera-txt;
                            width: fit-content;
                            padding: 1rem;
                            text-align: center;
                            vertical-align: middle;
                        }

                        .cabecera:hover {
                            background-color: v.$tabla-cabecera-hover;
                        }
                    }
                }

                .tabla__cuerpo {
                    tr {

                        .nombreInvi,
                        .correo {
                            text-align: center;
                            vertical-align: middle;
                            padding: 1rem 4rem 1rem 4rem;

                            @media (max-width: v.$phone) {
                                padding: 1rem;
                            }
                        }
                    }
                }
            }
        }
    }

    .boton {
        display: flex;
        align-content: center;
        flex-wrap: nowrap;

        button {
            transition: v.$tran-btn;
            font-weight: 500;
            text-decoration: none;
            font-size: 20px;
            letter-spacing: 0.1rem;
            vertical-align: middle;
            border: none;
            border-radius: 1rem;
            //padding: 1rem 2rem;
        }

        .aggInvitado__btn {
            margin: 2.5rem 1.5rem 0rem 1.5rem;
            width: 20%;
            height: 4rem;
            background-color: v.$secundario;
            color: v.$negro;

            @media (max-width: v.$phone) {
                height: 4rem;
                font-size: 18px;
                padding: 0.5rem 1rem;
            }

            &:hover {
                background-color: v.$secundario-hover;
                color: v.$blanco;
                transition: v.$tran-btn;
            }
        }

        @media (max-width: 832px) {
            flex-direction: column;

            .aggInvitado__btn {
                width: 100%;
                margin: 1rem auto;
            }
        }
    }

    .agregarCorreo {
        .Ninput {
            @include m.input(v.$blanco, #ecedec, v.$primario, v.$gri);
        }

        .Nlabel {
            font-size: 1.6rem;

            @include m.tablet {
                font-size: 2rem;
                max-width: calc(100% - 40px);

            }
        }

        .anadirbtn {
            transition: v.$tran-btn;
            font-weight: 500;
            text-decoration: none;
            font-size: 20px;
            letter-spacing: 0.1rem;
            vertical-align: middle;
            border: none;
            border-radius: 1rem;
            width: 50%;
            height: 4rem;
            background-color: v.$primario;
            color: v.$blanco;

            @media (max-width: v.$phone) {
                height: 4rem;
                font-size: 18px;
                padding: 0.5rem 1rem;
            }

            &:hover {
                background-color: v.$primario-hover;
                color: v.$blanco;
                transition: v.$tran-btn;
            }
        }

        @media (min-width: 832px) {
            .conboton {
                margin-top: 4.8rem;
                margin-left: 1rem;
            }
        }

        @media (max-width: 832px) {
            flex-direction: column;

            .anadir__btn {
                width: 60%;
                margin: 1rem auto;
            }
        }
    }
}