@use 'Base/variables' as v;
@use 'Base/mixins' as m;

.containerIndex{
    display: block;
    width: 80%;
    align-items: center;
    margin: auto;
    position: relative;
     @include m.tablet{
        width: 100%;
        display: flex;
        justify-content: space-between;
     }
    
        .indexizq{
        
            h1{
                text-align: center;
                font-size: 5rem;
            }
            h2{
                color:v.$primario;
            }
            @include m.tablet{
                margin-bottom: 0%;
                width: 50%;
            }
            
        }
        .indexDer{
            display: block;
            margin: auto;
            position: relative;
            @include m.tablet{
                padding: 5%;
                width: 50%;
            }
            .panales{
                margin: auto;
                width: auto;
                height: 500px;
                position: relative;
            // .recuperarConEnlace
            // {
            //     margin-top: 0%;
            //     align-items: center;
            //     text-align: center;
            //     a{  
            //         cursor: pointer;
            //         font-size: 1.2rem;
            //         color: v.$negro;
            //         @include m.tablet{
            //             font-size: 2rem;
            //         }
            //     }
            // }
            .contenedor-panal-uno {
                position: absolute;
                width: 230px;
                height: 250px;
                background-color: #FFDD97;
                top: 30%;
                left: 30%;
                transform: translate(-50%, -50%);
                clip-path: polygon(50% 0%, 100% 25%, 100% 75%, 50% 100%, 0% 75%, 0% 25%);
                @include m.tablet{
                    width: 220px;
                    height: 240px;
                    top:35%;
                    left:40%;
                }
                @include m.desktop{
                    width: 230px;
                    height: 250px;
                    top:40%;
                    left:35%;
                }
                    &:hover{
                        background-color: #f0c380;
                    }
            }
            .contenedor-panal-dos {
                position: absolute;
                background-color: #f0c380;
                transform: translate(-50%, -50%);
                clip-path: polygon(50% 0%, 100% 25%, 100% 75%, 50% 100%, 0% 75%, 0% 25%);
                width: 220px;
                height: 240px;
                top: 70%;
                left: 70%;
                    @include m.tablet{
                        width: 220px;
                        height: 240px;
                        top: 75%;
                        left: 85%;
                    }
                    @include m.desktop{
                        width: 230px;
                        height: 250px;
                        top:80%;
                        left:60%;
                    }
                    &:hover{
                        background-color:#f2c069 ;
                    }
            }
        }
    }
            .textEnlaceIndex{
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                text-align: center;
            }
            .textEnlaceIndex p{
                font-weight: bold;
                color: v.$primario;
                font-size: 1.5rem;
                margin: 0;
                @include m.tablet{
                    font-size: 2rem;
                }
               
            }
            
        }
